//var globalVar=require('./core/global-variable.js');//
require("./core/main-nav.js");

//
window.onload=function(){
    parallaxReset();
};
function parallaxReset(){
    var parallaxGather=$('[data-parallax-con]');
    var parallaxGatherItem=$('[data-parallax-item]');
    var winH=$(window).height();
    var bodyH=$('body').height();
    parallaxGather.each(function(){
        var parallaxConH=$(this).height();
        var parallaxOffT=$(this).offset().top;
        var parallaxOffH=parallaxOffT-winH;
        var parallaxItem=$(this).find('[data-parallax-item]');
        parallaxItem.each(function(){
            var diffH=parseInt($(this).height()-parallaxConH);
            var start=parseInt(parallaxOffH);
            var end=parseInt(parallaxOffH+$(this).height());
            var scrollCan=bodyH-parallaxOffT;
            $(this).attr({'start':start,'end':end,'diff':diffH,'ratio':diffH/scrollCan,'scrollH':scrollCan});
        });
    });
    //
    $(window).scroll(function(){
        var scrollT=$(this).scrollTop();
        parallaxGatherItem.each(function(){
            var scrollDiff=scrollT-$(this).attr('start');
            var radio=$(this).attr('ratio');
            if(scrollDiff>=0){
                $(this).css({'webkitTransform':'translateY(-'+scrollDiff*radio+'px)','transform':'translateY(-'+scrollDiff*radio+'px)'});
            }
        })
    })
}


